.CreateTodoButton {
  z-index: 1;
  background-color: #FAFAFA;
  box-shadow: 0px 2px 5px 0px rgb(0 0 0 / 50%);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  position: fixed;
  bottom: 7px;
  right: 7px;
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;

  transform: rotate(0);
  transition: .3s ease;
}
  
.CreateTodoButton:hover {
  transform: rotate(224deg);
}
.CreateTodoButton span {
  display: grid;
  align-items: center;
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.CreateTodoButton-color-blue {
  background-color: var(--blue);
}
.CreateTodoButton-color-pink {
  background-color: var(--pink);
}
.CreateTodoButton-color-yellow {
  background-color: var(--yellow);
}